.page-header {
  height: 860px;
  background-image: url("../img/page-header-background@1x.png");
  background-position: center, center;
  background-repeat: no-repeat;

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    background-image: url("../img/page-header-background@2x.png");
  }
}

.page-header__wrapper {
  padding: 185px 150px 0 700px;
}

.page-header__title {
  margin: 0 0 30px 0;
  padding: 0;
  font-weight: 600;
  font-size: 55px;
  line-height: 80px;
  letter-spacing: -1.11px;
  color: $white;
}

.page-header__descritpion {
  margin: 0 0 39px 0;
  padding: 0 20px 0 0;
  font-size: 16px;
  line-height: 27px;
  letter-spacing: -0.47px;
  color: $white;
}

.page-header__button {
  padding: 30px 40px 30px 40px;
  display: flex;
  justify-content: space-between;
  width: 340px;
}

.page-header__button-icon {
  width: 14px;
  height: 14px;
  fill: none;
}
